
*, ::after, ::before {
  user-select: none;
  box-sizing: border-box;
}

body {
  background-color: #222;
  
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .ahmed {
    width: 300px;
    height: 380px;
    background: #2196F3;
    border-radius: 30px;
    padding: 30px;
    transform: scale(2.2);
    .num {
      background: #00000059;
      text-align: center;
      border-radius: 12px;
      width: 70%;
      margin: 30px auto 0;
      span {
        // color: #f3f3f3;
        color: red;
        font-size: 36px;
        font-family: 'Sarpanch', sans-serif;
      }
    }
    .count {
      position: absolute;
      left: calc(50% - 60px);
      bottom: 80px;
      width: 120px;
      height: 120px;
      background: #002b4d;
      border-radius: 50%;
      outline: none;
      text-align: center;
      margin: 80px auto 0;
      display: block;
      cursor: pointer;
      border: 0;
      transition: all .2s;
    }
    .reset {
      display: block;
      text-align: center;
      float: right;
      margin: 0;
      position: absolute;
      bottom: 52%;
      right: 50px;
      background: #00000070;
      width: 40px;
      height: 40px;
      cursor: pointer;
      border-radius: 50%;
      border: 0;
      outline: 0;
    }
  }
}